<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right" >
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item ></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
               <el-row :gutter="10">
                   <el-col :span="16">
                        <el-input placeholder="" clearable v-model="queryInfo.query"  @keyup.enter.native="getUserList" @clear="getUserList">
                            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
                        </el-input>
                   </el-col>
                   <el-col :span="4">
                       <el-button type="primary"  @click="dialogFormVisible = true"></el-button>
                   </el-col>
               </el-row>
               <!--  -->
               <el-table stripe :data="userList" border highlight-current-row style="width: 100%" height="450px">
                   <el-table-column  type="index" label="#"></el-table-column>
                   <el-table-column prop="username" label="" width="80"></el-table-column>
                   <el-table-column prop="email" label="" width="180"></el-table-column>
                   <el-table-column prop="mobile" label="" width="180"></el-table-column>
                   <el-table-column prop="role_name" label="" width="180"></el-table-column>
                   <el-table-column prop="mg_state" label="" width="180">
                    <!--  switch -->
                        <template slot-scope="scope">
                             <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
                        </template>
                   </el-table-column>
                   <el-table-column  label="" width="180">
                       <template slot-scope="scope">
                           <!--  -->
                            <el-button type="primary" icon="el-icon-edit" circle  @click="showEditDialog(scope.row.id)"></el-button>
                            <!--  -->
                            <el-tooltip effect="dark" content="" placement="top">
                                <el-button type="danger" icon="el-icon-delete" circle @click="deleteUser(scope.row.id)"></el-button>
                            </el-tooltip>
                            <!--  -->
                            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                                <el-button type="warning" icon="el-icon-star-off" circle></el-button>
                            </el-tooltip>
                       </template>
                   </el-table-column>
               </el-table>
               <!--  -->
                 <div class="block" >
                    <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="queryInfo.pagenum"
                    :page-sizes="[1, 3, 5, 10]"
                    :page-size="queryInfo.pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                    </el-pagination>
                </div>
                <!--  -->
                <el-dialog title="" :visible.sync="dialogFormVisible" @close="addDialogClose" width="70%">
                    <el-form label-width="80px" :model="addForm" ref="addFormRef" :rules="rules">
                        <el-form-item label=""  prop="username" >
                            <el-input v-model="addForm.username"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop='password'>
                             <el-input  v-model="addForm.password" ></el-input>
                        </el-form-item>
                        <el-form-item label="" prop ='email'>
                             <el-input  v-model="addForm.email"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop='mobile' >
                             <el-input  v-model="addForm.mobile"></el-input>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button round @click="dialogFormVisible = false"> </el-button>
                        <el-button type="danger" round @click="resetAddForm"></el-button>
                        <el-button type="primary" round @click="addUser"> </el-button>
                    </div>
                </el-dialog>
                <!--  -->
                <el-dialog title="" :visible.sync="editUserVisible" width="70%">
                    <el-form label-width="80px" :model="editForm" ref="editFormRef" :rules="rules">
                        <el-form-item label=""  prop="username"  >
                            <el-input v-model="editForm.username" disabled></el-input>
                        </el-form-item>
                        <el-form-item label=""  prop="email"  >
                            <el-input v-model="editForm.email"></el-input>
                        </el-form-item>
                        <el-form-item label=""  prop="mobile"  >
                            <el-input v-model="editForm.mobile"></el-input>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button type="info" round @click="editUserVisible = false"></el-button>
                        <el-button type="primary" round @click="editUser" ></el-button>
                    </div>
                </el-dialog>
        </el-card>
    </div>
</template>
<script>
export default {
    data(){
        // 
        const validatePass = (rule, value, callback) => {
            if (value.trim() === '' && value.length === 0){
                console.log(rule)
                callback(new Error(''))
            } else {
                //  6 16
                // 
               const regx = new RegExp('(?=.*[0-9])(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9]).{6,16}')
               if ( !regx.test(value)){
                   callback(new Error(''))
               } else {
                   callback()
               }
            }
        }
        // 
        const validateEmail = (rule, value, callback) => {
            //  -
            // @ " . "2-4
            const regx = new RegExp('^([\\da-zA-Z])(\\w|\\-)+@[\\da-zA-Z]+\\.([a-zA-Z]{2,4})$')
            if (value.trim() === ''){
                callback()
            } else if (!regx.test(value)){
                callback(new Error(''))
            } else {
                callback()
            }
        }
        // 
        const validateMobile = (rule, value, callback) => {
            const regx = new RegExp('^(0|\\+?86|17951)?(13[0-9]|15[012356789]|17[013678]|18[0-9]|14[57])[0-9]{8}$')
            if (!regx.test(value)) {
                callback(new Error(''))
            } else {
                callback()
            }
        }
        return {
            queryInfo:{
                //query
                query:'',
                pagenum:1,
                pagesize:5
            },
              addForm:{
                username:'',
                password:'',
                email:'',
                mobile:''
            },
            editForm:{
            },
            userList:[],
            total:0,
            dialogFormVisible: false,
            editUserVisible:false,
            formLabelWidth:'120px',
            rules:{
                username:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 2, max: 15, message: ' 2  15 ', trigger: 'blur' }
                    ],
                password: [
                    { required: true },
                    { validator: validatePass, trigger: 'blur' }
                ],
                email: [
                    { validator: validateEmail, trigger: 'blur' }
                ],
                mobile: [
                    { required:true, message: '', trigger: 'blur' },
                    { validator: validateMobile, trigger: 'blur' }
                ]
            },
        }
    },
    created() {
        //  
        this.getUserList()
    },
    methods: {
        // 
        async getUserList(){
          const { data: res } = await this.$http.get('users', {
              params:this.queryInfo
           })
           if (res.meta.status !== 200 ){
                return this.$message.error(res.meta.msg)
            }
            this.userList = res.data.users
            this.total = res.data.total
        },
        // 
        handleSizeChange(newSize){
            this.queryInfo.pagesize = newSize
            this.getUserList()
        },
        //
        handleCurrentChange(currentPage){
            this.queryInfo.pagenum = currentPage
            this.getUserList()
        },
        //  element ui change
        async userStateChanged(userInfo){
            console.log(userInfo)
           await this.$http.put(`users/:${userInfo.id}/state/:${userInfo.type}`)
            console.log(userInfo)
        },
        // 
        resetAddForm(){
            this.$refs.addFormRef.resetFields()
        },
        // dialog
        addDialogClose(){
             this.$refs.addFormRef.resetFields()
        },
        // 
         addUser(){
             console.log(this.$refs)
             this.$refs.addFormRef.validate( async valid => {
                 // js
                 if (!valid) return false
                //
              const { data:res } = await this.$http.post('users', this.addForm)
              if (res.meta.status !== 201){
                  return this.$message.error(res.meta.msg)
              }
                  this.$message.success(res.meta.msg)
                  this.dialogFormVisible = false
                  this.getUserList()
             })
        },
        //
        async showEditDialog(id){
            // id
            this.editUserVisible = true
            const { data:res } = await this.$http.get(`users/${id}`)
            if (res.meta.status !== 200 ){
               return this.$message.error('')
            }
            this.editForm = res.data
        },
        // 
       deleteUser(id){
            this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
            }).then( async () => {
                const { data: res } = await this.$http.delete(`users/${id}`)
                if (res.meta.status !== 200) {
                    this.$message.error(res.meta.msg)
                }
                this.$message.success(res.meta.msg)
                this.getUserList()
            }).catch(() => {
            this.$message({
                type: 'info',
                message: ''
            })
            })
        },
        async editUser(){
            const id = this.editForm.id
            const { data:res } = await this.$http.put(`users/${id}`, this.editForm)
            if (res.meta.status !== 200) {
                console.log(res)
                return this.$message.error('')
            }
            this.editUserVisible = false
            this.$message.success(res.meta.msg)
            console.log(this.editForm)
             this.getUserList()
        }
    },
}
</script>
<style lang="less" scoped>
.el-card{
    .block{
        overflow-x: auto !important;
        }
    .dialog-footer{
            margin-top: -30px !important;
            display: flex !important;
            justify-content:space-evenly !important;
    }
}
</style>
