<template>
    <div>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <el-row :gutter="10">
                <el-col :span="8" style="min-width:260px">
                    <el-input placeholder="" class="input-with-select" v-model="searchValue" @keyup.enter.native="getSearchOrder">
                          <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="16">
                    <el-pagination
                         layout="total, sizes, prev, pager, next, jumper"
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="queryInfo.pagenum"
                        :page-sizes="[5, 10, 20, 50]"
                        :page-size="20"
                        :total="total">
                    </el-pagination>
                </el-col>
            </el-row>
            <el-table
                :data="orderList"
                stripe
                border
                height="500px"
                >
                <el-table-column
                    type="index"
                    label="#"
                    width="width">
                </el-table-column>
                <el-table-column
                    prop="order_number"
                    label="" >
                </el-table-column>
                <el-table-column
                    prop="order_price"
                    label="" >
                </el-table-column>
                <el-table-column
                    label="" >
                    <template slot-scope="scope">
                        <el-tag effect="dark" size="mini" v-if="Number(scope.row.order_pay) === 1"></el-tag>
                        <el-tag effect="dark" type="danger" size="mini" v-else></el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="is_send"
                    label="" >
                </el-table-column>
                <el-table-column
                    label="" >
                    <template slot-scope="scope">
                        {{scope.row.create_time | dateFormat}}
                    </template>
                </el-table-column>
                <el-table-column
                    width="120px"
                    label="" >
                    <template>
                        <el-tooltip class="item" effect="dark" content="" placement="top">
                            <el-button type="primary" icon="el-icon-edit" size="mini"  @click="showAddress">
                        </el-button>
                        </el-tooltip>
                        <el-tooltip effect="dark" content="" placement="top">
                            <el-button type="success" icon="el-icon-location" size="mini" @click="showProgressDialog"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-dialog
                title=""
                :visible.sync="addressVisible"
                width="50%"
                @close="addressDialogClose"
                >
                <div>
                    <el-form ref="addressFormRef" :rules="rules" :model="addressForm" label-width="85px">
                        <el-form-item label="/" prop="address1">
                        <el-cascader
                            :options="cityData"
                            v-model="addressForm.address1"
                            >
                        </el-cascader>
                        </el-form-item>
                        <el-form-item label="" prop="address2">
                        <el-input v-model="addressForm.address2" placeholder=""></el-input>
                        </el-form-item>
                    </el-form>
                </div>
                <div slot="footer">
                    <el-button @click="addressVisible = false"> </el-button>
                    <el-button type="primary" @click="addressVisible = false"> </el-button>
                </div>
            </el-dialog>
            <!--  -->
            <el-dialog
                title=""
                :visible.sync="progressVisible"
                width="50%"
                >
                <div>
                    <el-timeline :reverse="false">
                        <el-timeline-item
                        v-for="(item, index) in progressInfo"
                        :key="index"
                        :timestamp="item.ftime"
                        color="skyblue"
                        >
                        {{item.context}}
                        </el-timeline-item>
                    </el-timeline>
                </div>
                <div slot="footer">
                    <el-button @click="progressVisible = false"> </el-button>
                    <el-button type="primary" @click="progressVisible  = false"> </el-button>
                </div>
            </el-dialog>
        </el-card>
    </div>
</template>
<script>
import cityData from './citydata.js'
export default {
    data() {
        return {
            queryInfo:{
                query:'',
                pagenum:1,
                pagesize:20
            },
            orderList:[],
            total:0,
            searchValue:'',
            addressVisible:false,
            addressForm:{
                address1:'',
                address2:''
            },
            rules:{
                address1:[
                    { required:true, message:'', trigger:'blur' }
                ],
                address2:[
                    { required:true, message:'', trigger:'blur' }
                ]

            },
            cityData,
            progressVisible:false,
            progressInfo:[]
        }
    },
    methods: {
        async getOrderList(){
            const { data: res } = await this.$http.get('orders', {
                params:this.queryInfo
            })
            if ( res.meta.status !== 200 ) {
                return this.$message.error('')
            }
            console.log(res.data)
            this.total = res.data.total
            this.orderList = res.data.goods
        },
        // 
        handleSizeChange(newSize){
            this.queryInfo.pagesize = newSize
            this.getOrderList()
        },
        // 
        handleCurrentChange(newPagenum){
            this.queryInfo.pagenum = newPagenum
            this.getOrderList()
        },
        getSearchOrder(){
            this.queryInfo.query = this.searchValue
            this.getOrderList()
        },
        showAddress(){
            this.addressVisible = true
        },
        // 
        async showProgressDialog(){
            const { data: res } = await this.$http.get('/kuaidi/1106975712662')
            if (res.meta.status !== 200) return this.$message.error('')
            console.log(res)
            this.progressInfo = res.data
            this.progressVisible = true
        },
        addressDialogClose(){
            this.$refs.addressFormRef.resetFields()
        }
    },
    created() {
        this.getOrderList()
    },
}
</script>
<style lang="css">
    .el-cascader{
        width: 100%;
    }
    .el-pagination{
        min-width: 300px;
        overflow: auto;
        margin-top: 5px;
    }
</style>
