<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card style="overflow:auto;width:90%;height:100%">
            <div id="main"></div>
        </el-card>
    </div>
</template>
<script>
import * as echarts from 'echarts/core'
//  Chart
import { LineChart } from 'echarts/charts'
//  Component
import {
    TitleComponent,
    TooltipComponent,
    GridComponent,
    LegendComponent
} from 'echarts/components'
//  Canvas  CanvasRenderer  SVGRenderer 
import {
    CanvasRenderer
} from 'echarts/renderers'
echarts.use(
    [TitleComponent, TooltipComponent, GridComponent, LineChart, CanvasRenderer, LegendComponent]
)
export default {
    // echarts
    data(){
        return {
        options: {
                title: {
                text: ''
                },
                tooltip: {
                trigger: 'axis',
                axisPointer: {
                    type: 'cross',
                    label: {
                    backgroundColor: '#E9EEF3'
                    }
                }
                },
                grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
                },
                xAxis: [
                {
                    boundaryGap: false
                }
                ],
                yAxis: [
                {
                    type: 'value'
                }
                ],
            }
        }
    },
    created(){},
    async mounted(){
        var myChart = echarts.init(document.getElementById('main'))
        const { data:res } = await this.$http.get('reports/type/1')
        if (res.meta.status !== 200){
            this.$message.error('')
        }
        // 

        // 
        console.log(res.data)
        // this.options
        Object.assign(this.options, res.data)
        myChart.setOption(this.options)
    }
}
</script>
<style scoped>
#main {
    width: 100%;
    min-width:480px;
    height: 550px;
}
</style>
