<template>
    <div style="width:100%">
           <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right" >
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item ></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card" style="min-width:625px">
            <el-button type="primary" @click="roleDialogVisible = true; addRoleBtn ='' "></el-button>
            <!--  -->
            <el-table
                :data="rolesData"
                highlight-current-row
                border
                stripe
                >
                 <el-table-column prop="prop" width="30" type="expand">
                  <template slot-scope="scope">
                      <el-row :gutter="10" :class="['rightlevel']">
                          <el-col :span="6">
                              <span></span>
                          </el-col>
                          <el-col :span="18">
                              <el-row>
                                  <el-col :span="8"><span></span></el-col>
                                  <el-col :span="16"><span></span></el-col>
                              </el-row>
                          </el-col>
                      </el-row>
                      <!--  -->
                      <el-row :class="['bd_bottom','vcenter',i1 === '0' ? 'bd_top' : '']" :gutter="10" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                          <el-col :span="6">
                              <el-tag  effect="dark" :closable="tagClosable" @close="removeRight(scope.row,item1.id)">{{item1.authName}}</el-tag>
                              <i class="el-icon-caret-right"></i>
                          </el-col>
                          <el-col :span="18">
                              <!--  -->
                              <el-row v-for="(item2,i2) in item1.children" :class="['vcenter',i2 === '0' ? '' : 'bd_top']" :key="item2.id">
                                  <el-col :span="8">
                                      <el-tag  effect="dark" type="success" closable @close="removeRight(scope.row,item2.id)">{{item2.authName}}</el-tag>
                                     <i class="el-icon-caret-right"></i>
                                  </el-col>
                                     <!--  -->
                                  <el-col :span="16">
                                    <el-tag effect="dark" type="danger" closable @close="removeRight(scope.row,item3.id)" v-for="(item3,i3) in item2.children" :class="[i3 === '0' ? '' : 'bd_top']" :key="item3.id">{{item3.authName}}</el-tag>
                                  </el-col>
                              </el-row>
                          </el-col>
                      </el-row>
                  </template>
                </el-table-column>
                <el-table-column
                    type="index"
                    label="#"
                    width='30'
                   >
                </el-table-column>
                <el-table-column
                    prop="roleName"
                    label=""
                   >
                </el-table-column>
                <el-table-column
                    prop="roleDesc"
                    label=""
                    >
                </el-table-column>
                <el-table-column  class="table_col_btn" label=""  width="320">
                            <template slot-scope="scope">
                           <!--  -->
                            <el-button type="primary" icon="el-icon-edit" @click="editRoleDialogVisible(scope.row.id)" ></el-button>
                            <!--  -->
                            <el-tooltip effect="dark" content="" placement="top">
                                <el-button type="danger" icon="el-icon-delete" @click="deleteRole(scope.row.id)"></el-button>
                            </el-tooltip>
                            <!--  -->
                            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                                <el-button type="warning" icon="el-icon-star-off" @click="showRightDialog(scope.row)"></el-button>
                            </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-dialog
                    :title="addRoleBtn"
                    :visible.sync="roleDialogVisible"
                    width="70%"
                    @close="roleDialogReset"
                    >
                        <el-form :model="addRoleData" ref="addRoleDataRef" :rules="rules" label-width="80px">
                            <el-form-item label="" prop='roleName'>
                                <el-input v-model="addRoleData.roleName"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop='roleDesc'>
                                <el-input v-model="addRoleData.roleDesc"></el-input>
                            </el-form-item>
                        </el-form>
                    <div slot="footer">
                        <el-button @click="roleDialogVisible = false"> </el-button>
                        <el-button type="primary" @click="addRole" v-if="addRoleBtn === ''"> </el-button>
                        <el-button type="primary" @click="editRole(addRoleData.roleId)" v-else> </el-button>
                    </div>
            </el-dialog>
            <!--  -->
            <el-dialog title="" :visible.sync="rightDialogVisible" width="50%" @close="resetDefKeys">
                <!--  -->
                <el-tree ref="treeRef" :data="rightList" :props="treeProps" show-checkbox default-expand-all :default-checked-keys='defKeys' node-key="id" >
                </el-tree>
                <div slot="footer">
                    <el-button @click="rightDialogVisible = false"> </el-button>
                    <el-button type="primary" @click="allotRights"> </el-button>
                </div>
            </el-dialog>
        </el-card>
    </div>
</template>
<script>
export default {
    data(){
        return {
            rolesData:[],
            roleDialogVisible:false,
            rightDialogVisible:false,
            tagClosable:true,
            handleClose:false,
            addRoleBtn:'',
            addRoleData:{
                roleName:'',
                roleDesc:'',
                // roleId:''
            },
            rules:{
                roleName:[
                    { required: true, message:'', trigger: 'blur' }
                ],
                roleDesc:[
                    { required:true, message:'', trigger: 'blur' }
                ]
            },
            rightList:[],
            treeProps:{
                label:'authName',
                children:'children',
            },
            defKeys:[],
            // id
            roleId:''
        }
    },
    created() {
       this.getRolesList()
    },
    methods: {
        // 
        getRolesList(){
            this.$http.get('roles').then((result) => {
            const { data: res } = result
            if (res.meta.status !== 200 ){
               return this.$message.error(res.meta.msg)
            }
            this.rolesData = res.data
             })
        },
        // 
        addRole(){
            this.$refs.addRoleDataRef.validate( async valid => {
                 // js
                 if (!valid) return false
                //
              const { data:res } = await this.$http.post('roles', this.addRoleData)
              if ( res.meta.status !== 201){
                  return this.$message.error(res.meta.msg)
              }
                this.roleDialogVisible = false
                  this.$message.success(res.meta.msg)
                  this.getRolesList()
             })
        },
        // 
        roleDialogReset(){
              this.$refs.addRoleDataRef.resetFields()
        },
        // awaitpromise
        //confirm
        async confirm(delMsg, delTitle){
            const confirmResult = await this.$confirm(delMsg, delTitle, {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
          }).catch(err => err)
          return confirmResult
        },
        async removeRight(role, rightId){
            if (await this.confirm('?', '') !== 'confirm'){
                return this.$message.info('')
            }
            const { data:res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
            console.log(res)
            role.children = res.data
        },
        //  id
        // :  
        async editRoleDialogVisible(roleId){
            this.addRoleBtn = ''
            const { data:res } = await this.$http.get(`roles/${roleId}`)
            this.addRoleData = res.data
            this.roleDialogVisible = true
        },
        //  
        async editRole(id) {
            const { data:result } = await this.$http.put(`roles/${id}`, this.addRoleData)
            if (result.meta.status !== 200 ) {
              return this.$message.error('')
            }
            this.roleDialogVisible = false
            this.$message.success('')
            this.getRolesList()
        },
        // id
        async deleteRole(roleId){
             if (await this.confirm('', '') !== 'confirm'){
                return this.$message.info('')
            }
            const { data:res } = await this.$http.delete(`roles/${roleId}`)
            this.$message.success(res.meta.msg)
            this.getRolesList()
        },
        //
        async showRightDialog(role){
            this.roleId = role.id
            const { data:res } = await this.$http.get('rights/tree')
            if (res.meta.status !== 200 ){
                return this.$message.error(res.meta.msg)
            }
            this.rightList = res.data
            this.getLesfKeys(role, this.defKeys)
            console.log(this.defKeys)
            this.rightDialogVisible = true
        },
        // id defKeys
        getLesfKeys(node, arr){
            // children 
            if ( !node.children) {
                return arr.push(node.id)
            }
            node.children.forEach(item => {
                  this.getLesfKeys(item, arr)
            })
        },
        //
        resetDefKeys(){
            this.defKeys = []
        },
        async allotRights(){
            const Keys = [
                ...this.$refs.treeRef.getCheckedKeys(),
                ...this.$refs.treeRef.getHalfCheckedKeys()
            ]
            const idStr = Keys.join(',')
            const { data:res } = await this.$http.post(`roles/${this.roleId}/rights`, {
                rids:idStr
            })
            if (res.meta.status !== 200){
                return this.$message.error('')
            }
            this.$message.success('')
            this.rightDialogVisible = false
            this.getRolesList()
        }
    },
}
</script>
<style lang="less" scoped>
    .table_col_btn{
        display: flex;
        justify-content: left;
        overflow-x: auto;
    }
    .el-table{
        overflow-x: auto;
        .el-button{
         font-size: 6px;
        padding: 6px 20px;
        }
    }
    .el-tag{
        margin:7px;
    }
    .bd_top{
        border-top: 1px solid #eee;
    }
    .bd_bottom{
        border: 1px solid #eee;
    }
    .vcenter{
        display: flex;
        align-items: center;
    }
    .rightlevel{
        text-align: center;
        font-weight: bold;
        padding-bottom: 8px;
    }
</style>
