<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"> </el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <el-alert
                title=""
                type="info"
                center
                :closable="false"
                show-icon
                >
            </el-alert>
            <!--  -->
            <el-steps  :active="activeIndex - 0" finish-status="success" simple align-center >
                <el-step title="" icon="el-icon-edit"></el-step>
                <el-step title="" icon="el-icon-goods"></el-step>
                <el-step title="" icon="el-icon-s-goods"></el-step>
                <el-step title="" icon="el-icon-picture"></el-step>
                <el-step title="" icon="el-icon-s-goods"></el-step>
                <el-step title="" icon="el-icon-success"></el-step>
            </el-steps>
            <!-- tab  -->
            <el-form style="overflow:auto;height:620px" ref="addFormRef" :model="addForm" label-width="80px" label-position="top" :rules="addFormRules">
                <el-tabs v-model="activeIndex" tab-position="left" :before-leave="beforeTabLeave" @tab-click="tabClicked">
                    <el-tab-pane label="" name="0">
                        <el-form-item label="" prop="goods_name">
                            <el-input v-model="addForm.goods_name"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_price">
                            <el-input v-model="addForm.goods_price" type="number"></el-input>
                        </el-form-item>
                          <el-form-item label="" prop="goods_weight">
                            <el-input v-model="addForm.goods_weight" type="number"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_number">
                            <el-input v-model="addForm.goods_number" type="number"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_cat">
                            <el-cascader
                                placeholder=""
                                clearable
                                :options="cateList"
                                :props="cateProps"
                                v-model="addForm.goods_cat"
                                @change="handleChange">
                            </el-cascader>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="" name="1">
                        <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
                            <el-checkbox-group v-model="item.attr_vals">
                                <el-checkbox border  :label="cb" :key="i" v-for="(cb, i) in item.attr_vals"></el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="" name="2">
                        <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
                            <el-checkbox-group v-model="item.attr_vals">
                                <el-checkbox border :label="cb" v-for="(cb, i) in item.attr_vals" :key="i"></el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="" name="3">
                        <el-upload
                        :on-success="handleSuccess"
                        :headers="headers"
                        :action="uploadUrl"
                        :on-preview="handlePreview"
                        :on-remove="handleRemove"
                        list-type="picture-card">
                          <i class="el-icon-plus"></i>
                        </el-upload>
                    </el-tab-pane>
                    <el-tab-pane label="" name="4">
                        <!--  -->
                        <quill-editor v-model="addForm.goods_introduce"></quill-editor>
                        <el-button type="primary" @click="add"></el-button>
                    </el-tab-pane>
                </el-tabs>
            </el-form>
            <!--  -->
            <el-dialog :visible.sync="previewVisible" width="80%">
                <img width="100%" :src="previewPath" alt="">
            </el-dialog>
        </el-card>
    </div>
</template>

<script>
export default {
    data() {
        return {
            activeIndex:'0',
            addForm:{
                goods_name:'',
                goods_price:0,
                goods_weight:0,
                goods_number:0,
                goods_cat:[],
                pics:[],
                // 
                goods_introduce:'',
                attrs:[]
            },
            cateList:[],
            // 
            manyTableData:[],
            // 
            onlyTableData:[],
            // url
            uploadUrl:'api/api/private/v1/upload',
            cateProps:{
                expandTrigger:'hover',
                label:'cat_name',
                value:'cat_id',
                children:'children'
            },
            // token
            headers:{
                Authorization:window.sessionStorage.getItem('token')
            },
            // 
            previewPath:'',
            previewVisible:false,
            addFormRules:{
                goods_name:[
                    { required:true, message:'', trigger:'blur' }
                ],
                goods_price:[
                    { required:true, message:'', trigger:'blur' }
                ],
                goods_weight:[
                    { required:true, message:'', trigger:'blur' }
                ],
                goods_number:[
                    { required:true, message:'', trigger:'blur' }
                ],
                goods_cat:[
                    { required:true, message:'', trigger:'blur' }
                ],
            }
        }
    },
    created() {
        this.getCateList()
    },
    methods: {
        async getCateList(){
            const { data:res } = await this.$http.get('categories')
            this.cateList = res.data
            console.log(res.data)
            if (res.meta.status !== 200 ){
                return this.$message.error('')
            }
        },
        handleChange(){
            if (this.addForm.goods_cat.length !== 3){
                this.addForm.goods_cat = []
                return this.$message.info('')
            }
            console.log(this.addForm.goods_cat)
        },
        beforeTabLeave(activeName, oldActiveName){
            if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3){
                this.addForm.goods_cat = []
                this.$message.error('')
                return false
            }
        },
        async tabClicked(){
            if (this.activeIndex === '1') {
            console.log('')
            const { data:res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
                params:{
                    sel:'many'
                }
             })
            if (res.meta.status !== 200){
                    return this.$message.error('')
                }
             res.data.forEach(item => {
                 item.attr_vals = item.attr_vals.trim().length === 0 ? [] : item.attr_vals.split(',')
             })
             this.manyTableData = res.data
            } else if (this.activeIndex === '2') {
                console.log('')
                const { data:res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
                    params:{
                        sel:'only'
                    }
                })
                console.log(res)
                if (res.meta.status !== 200){
                    return this.$message.error('')
                }
                res.data.forEach(item => {
                item.attr_vals = item.attr_vals.trim().length === 0 ? [] : item.attr_vals.split(',')
                })
                console.log(res.data)
                this.onlyTableData = res.data
            }
        },
        // 
        handlePreview(file){
            console.log(file)
            this.previewPath = file.response.data.url
            this.previewVisible = true
        },
        // 
        handleRemove(file){
            // 
            const filePath = file.response.data.tmp_path
            //  pics
            // splicepic
            console.log(filePath)
           const index = this.addForm.pics.findIndex(x => {
                x.pic === filePath
            })
            this.addForm.pics.splice(index, 1)
            console.log(this.addForm)
        },
        //  
        handleSuccess(response){
            console.log(response)
            // 
            const pictureInfo = {
                pic:response.data.tmp_path
            }
            // addform pics 
            this.addForm.pics.push(pictureInfo)
            console.log(this.addForm)
        },
        add(){
            this.$refs.addFormRef.validate(async valid => {
                if (!valid) {
                    return this.$message.error('')
                }
                // goods_cat
                this.addForm.goods_cat = this.addForm.goods_cat.join(',')
                // 
                this.manyTableData.forEach(item => {
                    const newManyInfo = {
                        attr_id:item.attr_id,
                        attr_value:item.attr_vals.join(',')
                    }
                    this.addForm.attrs.push(newManyInfo)
                })
                // 
                this.onlyTableData.forEach(item => {
                    const newOnlyInfo = {
                        attr_id:item.attr_id,
                        attr_value:item.attr_vals.join(',')
                    }
                    this.addForm.attrs.push(newOnlyInfo)
                })
                console.log(this.addForm)
                const { data:res } = await this.$http.post('goods', this.addForm)
                console.log(res)
                if (res.meta.status !== 200 ) {
                    return this.$message.error('')
                }
                this.$message.success('')
            })
        }
    },
    computed:{
            cateId(){
                if (this.addForm.goods_cat.length === 3) {
                    return this.addForm.goods_cat[2]
                }
                return null
            }
        }
}
</script>

<style lang="less" scoped>
    .el-steps {
        margin: 15px 0;
    }
    ::v-deep .el-step__title {
    font-size: 13px;
    }
    ::v-deep .el-cascader{
        .el-input {
            width: 200px;
        }
    }
    .el-checkbox{
        margin: 0 10px 0 0 !important;
    }
    .el-tab-pane{
        .el-button{
            margin-top: 12px;
        }
    }
</style>>
