<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-ico-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <div slot="header" class="card_header">
                <el-alert
                    title=""
                    type="warning"
                    :closable="false"
                    show-icon>
                </el-alert>
                <el-row class="cat_opt">
                    <el-col>
                        <span>:</span>
                        <el-cascader
                        :options="cateList"
                        v-model="cateKeys"
                        @change="cateSelectChange"
                        :props="cateProps"
                        clearable>
                        </el-cascader>
                    </el-col>
                </el-row>
            </div>
            <!-- body -->
            <div>
                <el-row>
                    <el-tabs type="border-card" v-model="activeName"  @tab-click="handleClick" style="overflow-x:auto">
                        <el-tab-pane label="" name="many">
                            <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="showAddDialog = true"></el-button>
                            <el-table
                                :data="manyParamsList" height="310"
                                 border  highlight-current-row stripe>
                                <el-table-column type="expand">
                                    <template slot-scope="scope">
                                        <el-tag type="success" @close="handleClose(index, scope.row)" closable v-for="(item, index) in scope.row.attr_vals" :key="index">
                                            {{item}}
                                        </el-tag>
                                         <el-input
                                            class="input-new-tag"
                                            v-if="scope.row.inputVisible"
                                            v-model="scope.row.inputValue"
                                            ref="saveTagInput"
                                            size="small"
                                            @keyup.enter.native="handleInputConfirm(scope.row)"
                                            @blur="handleInputConfirm(scope.row)"
                                            >
                                        </el-input>
                                        <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                                    </template>
                                </el-table-column>
                                <el-table-column
                                    label="#"
                                    type="index"
                                    >
                                </el-table-column>
                                <el-table-column
                                    prop="attr_name"
                                    label=""
                                    >
                                </el-table-column>
                                <el-table-column
                                    label=""
                                    width="180px"
                                    >
                                    <template slot-scope="scope">
                                    <el-button type="primary" size="mini" icon="el-icon-edit" @click="getParamName(scope.row.attr_id)"></el-button>
                                    <el-button type="danger" size="mini" icon="el-icon-delete"  @click="removeParams(scope.row.attr_id)"></el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </el-tab-pane>
                        <el-tab-pane label="" name="only">
                            <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="showAddDialog = true"></el-button>
                                <el-table
                                :data="onlyParamsList"
                                border  highlight-current-row stripe>
                                <el-table-column type="expand">
                                    <template slot-scope="scope">
                                        <el-tag type="success" @close="handleClose(index, scope.row)" closable v-for="(item, index) in scope.row.attr_vals" :key="index">
                                            {{item}}
                                        </el-tag>
                                         <el-input
                                            class="input-new-tag"
                                            v-if="scope.row.inputVisible"
                                            v-model="scope.row.inputValue"
                                            ref="saveTagInput"
                                            size="small"
                                            @keyup.enter.native="handleInputConfirm(scope.row)"
                                            @blur="handleInputConfirm(scope.row)"
                                            >
                                        </el-input>
                                        <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                                    </template>
                                </el-table-column>
                                <el-table-column
                                    label="#"
                                    type="index"
                                    width="30">
                                </el-table-column>
                                <el-table-column
                                    prop="attr_name"
                                    label=""
                                    >
                                </el-table-column>
                                <el-table-column
                                    label=""
                                    width="180"
                                    >
                                   <template slot-scope="scope">
                                        <el-button type="primary" size="mini" icon="el-icon-edit" @click="getParamName(scope.row.attr_id)"></el-button>
                                        <el-button type="danger" size="mini" icon="el-icon-delete" @click="removeParams(scope.row.attr_id)"></el-button>
                                   </template>
                                </el-table-column>
                            </el-table>
                        </el-tab-pane>

                    </el-tabs>
                </el-row>
            </div>
        </el-card>
        <!--  -->
        <el-dialog
            @close='closeDialog'
            :title=" '' + textTitle"
            :visible.sync="showAddDialog"
            width="60%"
            >
            <div>
                <el-form :model="addForm" ref="addFormRef" :rules="rules" >
                    <el-form-item :label="textTitle" prop="attr_name">
                        <el-input v-model="addForm.attr_name"></el-input>
                    </el-form-item>
                </el-form>
            </div>
            <div slot="footer">
                <el-button @click="showAddDialog = false"> </el-button>
                <el-button type="primary" @click="addParams"> </el-button>
            </div>
        </el-dialog>
        <!--  -->
        <el-dialog
            @close='closeEditDialog'
            :title=" '' + textTitle"
            :visible.sync="showEditDialog"
            width="60%"
            >
            <div>
                <el-form :model="editForm" ref="editFormRef" :rules="rules" >
                    <el-form-item :label="textTitle" prop="attr_name">
                        <el-input v-model="editForm.attr_name"></el-input>
                    </el-form-item>
                </el-form>
            </div>
            <div slot="footer">
                <el-button @click="showEditDialog = false"> </el-button>
                <el-button type="primary" @click="editParams"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import confirm from '../../assets/js/confirm'
export default {
    data() {
        return {
            // 
            cateList:[],
            // 
            cateProps:{
                expandTrigger:'hover',
                value:'cat_id',
                label:'cat_name',
                children:'children'
            },
            //  [cat_id]
            cateKeys:[],
            // tabs only many
            activeName:'many',
            // 
            manyParamsList:[],
            // 
            onlyParamsList:[],
            showAddDialog:false,
            addForm:{
                attr_name:''
            },
            rules:{
                    attr_name:[
                        { required:true, message:'', trigger:'blur' }
                    ]
            },
            showEditDialog:false,
            editForm:{},
            dynamicTags:[]
        }
    },
    created() {
        this.getCateList()
    },
    methods: {
        // 
        async getCateList(){
            const { data:res } = await this.$http.get('categories')
            this.cateList = res.data
            // console.log(res.data)
            if (res.meta.status !== 200 ){
                return this.$message.error('')
            }
        },
        // 
        cateSelectChange(){
            this.getParamsData()
        },
        // 
        async getParamsData(){
            if (this.cateKeys.length !== 3 ){
                this.cateKeys = []
                this.manyParamsList = []
                this.onlyParamsList = []
                return this.$message.warning('')
            }
            console.log(this.cateKeys)
            // 
            const { data:res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
                params:{
                    sel:this.activeName
                }
            })
            if (res.meta.status !== 200 ) {
                return this.$message.error('')
            }
            res.data.forEach(item => {
                item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
                item.inputVisible = false
                item.inputValue = ''
            })
            console.log(res.data)
            if (this.activeName === 'many') {
                    this.manyParamsList = res.data
            } else {
                    this.onlyParamsList = res.data
            }
        },
        // tabs
        handleClick(){
            this.getParamsData()
            console.log(this.activeName)
        },
        // 
        closeDialog(){
            this.$refs.addFormRef.resetFields()
        },
        closeEditDialog(){
            this.$refs.editFormRef.resetFields()
        },
        // 
        addParams(){
            this.$refs.addFormRef.validate( async vaild => {
                if (!vaild) return
                const { data:res } = await this.$http.post(`categories/${this.cateId}/attributes`, {
                    attr_name:this.addForm.attr_name,
                    attr_sel:this.activeName
                })
                if (res.meta.status !== 201 ) {
                    return this.$message.error(res.meta.msg)
                }
                this.showAddDialog = false
                this.$message.success(res.meta.msg)
                this.getParamsData()
            })
        },
        //
        async getParamName(id){
                const { data:res } = await this.$http.get(`categories/${this.cateId}/attributes/${id}`)
                if (res.meta.status !== 200 ) {
                    return this.$message.error(res.meta.msg)
                }
                this.editForm = res.data
                this.showEditDialog = true
        },
        // 
        editParams(){
            this.$refs.editFormRef.validate(async valid => {
                if (!valid) return
                const { data:res } = await this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`, {
                    attr_name:this.editForm.attr_name,
                    attr_sel:this.activeName
                })
                if (res.meta.status !== 200 ) {
                    return this.$message.error('')
                }
                this.getParamsData()
                this.showEditDialog = false
            })
        },
        async removeParams(id){
            if (await confirm(`${this.textTitle}?`, `${this.textTitle}`) !== 'confirm') {
                return this.$message.info(`${this.textTitle}`)
            }
            const { data:res } = await this.$http.delete(`categories/${this.cateId}/attributes/${id}`)
            if (res.meta.status !== 200) {
                this.$message.error(res.meta.msg)
            }
            this.$message.success(res.meta.msg)
            this.getParamsData()
        },
        //
        showInput(row) {
        row.inputVisible = true
        //$nextTick()
        //  inupt
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus()
        })
        },
      async handleInputConfirm(row) {
        if (row.inputValue.trim().length === 0) {
            row.inputVisible = false
            row.inputValue = ''
            return
        }
        //  return 
        console.log(row)
        row.attr_vals.push(row.inputValue.trim())
        row.inputVisible = false
        row.inputValue = ''
        // 
        this.saveAttrVals(row)
        },
        handleClose(index, row) {
          row.attr_vals.splice(index, 1)
          this.saveAttrVals(row)
        },
        async saveAttrVals(row){
                    // 
        const { data:res } = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
            attr_name:row.attr_name,
            attr_sel:row.attr_sel,
            attr_vals:row.attr_vals.join(',')
        })
        console.log(res)
        if (res.meta.status !== 200) {
            return this.$message.error('!')
        }
         this.$message.success('')
      }
    },
        //    id
    computed:{
        isBtnDisabled(){
            if (this.cateKeys.length !== 3 ){
                return true
            } else {
                return false
            }
        },
        // Id
        cateId() {
            if (this.cateKeys.length === 3 ){
                return this.cateKeys[2]
            }
            return null
        },
        textTitle(){
            if (this.activeName === 'many') {
                return ''
            } else {
                return ''
            }
        }
    }

}
</script>
<style lang="less" scoped>
    .card_header{
            margin-bottom: -10px;
            .cat_opt{
                span{
                    font-weight: normal;
                    margin-right: 15px;
                }
             margin: 15px 0;
        }
    }
    .el-tag{
        margin-top: 8px;
        margin-right: 15px;
    }
    .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
    }
    .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
    .el-cascader{
        width: 300px;
    }
</style>
